html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #F5F5F5;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0px;
}

p {
  color: black;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-centro {
  display: flex;
  justify-content: center;
}

.contenedor {
  width: 90%;
  margin: auto;
}

.alinear-izquierda {
  text-align: left;
}

.alinear-derecha {
  text-align: right;
}

.pequeno {
  max-width: 60%;
}

.no-margin {
  margin: 0;
}

.boton {
  display: inline-block;
  text-align: center;
  padding: 10px;
  margin: auto;
  font-size: 1.5rem;
  width: 15rem;
}
.boton:hover {
  cursor: pointer;
}

.verde {
  border: 2px solid #3d8b40;
  background-color: #4CAF50;
  color: white;
}

.rojo {
  border: 2px solid #b61219;
  background-color: #E51620;
  color: white;
}

.azul {
  border: 2px solid #0c2a60;
  background-color: #113E8E;
  color: white;
}

header {
  margin: 0;
  padding: 0;
}

.barra {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  background-color: #C0EDFF;
  padding-bottom: 1rem;
}
.barra .carrito img {
  width: 2.8rem;
  padding: 0;
}
.barra .logoPrincipal img {
  width: 15rem;
  padding: 1rem;
}
.barra .logoPrincipal img:hover {
  cursor: pointer;
}

.elementoMenu {
  display: flex;
  flex-direction: column;
  margin-right: 1rem;
}
.elementoMenu p {
  text-align: center;
  margin: 0;
  font-size: 1rem;
}

.navegacion {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navegacion a {
  padding: 5px 10px 5px 10px;
  color: black;
}
.navegacion a img {
  width: 2.8rem;
  padding: 0;
}
.navegacion a:last-child {
  padding-right: 2rem;
}
.navegacion:hover {
  cursor: pointer;
}

.footer {
  padding: 0.8333333333rem 0;
  background-color: #C0EDFF;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding: 1rem;
}
.footer .contenedor-footer .navegacion {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer .copyright {
  padding: 1rem;
  color: black;
  text-align: center;
}

.catalogo {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 1rem;
}

.imagen {
  display: block;
}
.imagen img {
  padding: 5px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.imagen img:hover {
  cursor: pointer;
}

.elemento {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border: 2px solid gainsboro;
  margin-bottom: 2rem;
  height: auto;
}

.contenido-libro {
  margin-top: 1rem;
  line-height: 1;
}
.contenido-libro p {
  font-size: 11px;
}
.contenido-libro .no-disponible {
  color: #E51620;
}
.contenido-libro .no-disponible span {
  font-weight: bold;
  color: black;
}
.contenido-libro .disponible {
  color: #4CAF50;
}
.contenido-libro .disponible span {
  font-weight: bold;
  color: black;
}

.botones {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 5px;
}
.botones a {
  margin-bottom: 5px;
}
.botones a img {
  width: 5rem;
}

.admin {
  margin-bottom: 2rem;
}

.prueba {
  display: none;
}
.prueba .carrito {
  display: flex;
  flex-direction: column;
}

.mostrar {
  display: initial;
  border-radius: 10px;
  padding: 1rem;
  background-color: darkgray;
  border: 5px solid #113E8E;
  z-index: 999;
  /*Subiendo el z-index del contenedor resuelves ese problema*/
  height: auto;
  width: 60rem;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

.busqueda {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.busqueda input {
  border: 1px solid #4CAF50;
  min-width: 50rem;
  width: auto;
  height: 3rem;
  text-indent: 2rem;
}
.busqueda label {
  font-weight: bolder;
  color: #4CAF50;
}

.formulario p {
  font-size: 1.4rem;
  color: #e1e1e1;
}
.formulario label {
  display: block;
  font-weight: bold;
  color: black;
  text-transform: uppercase;
}
.formulario legend {
  font-size: 2rem;
}
.formulario select {
  width: 15rem;
  height: 3rem;
  text-align: center;
}
.formulario input:not([type=submit]) {
  display: block;
  padding: 1rem;
  width: 100%;
  background-color: white;
  border: 1px solid #F5F5F5;
  margin-bottom: 1rem;
  border-radius: 1rem;
}
.formulario select {
  background-color: white;
  border: 1px solid #F5F5F5;
  margin-bottom: 1rem;
  border-radius: 1rem;
}
.formulario .enviar {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.usuario {
  margin-bottom: 2rem;
}

.ingresar {
  margin-bottom: 3rem;
}

.libroIsbn {
  display: none;
}
.libroIsbn select {
  text-align: center;
  width: 20rem;
}

.contenedor-libro {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 5rem;
  margin-bottom: 6rem;
  margin-top: 2rem;
}
.contenedor-libro .imagen-libro {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contenedor-libro .imagen-libro img {
  width: 25rem;
  height: 33rem;
}
.contenedor-libro .contenido-libro {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenedor-libro .contenido-libro p, .contenedor-libro .contenido-libro span {
  font-size: 1.5rem;
}
.contenedor-libro .contenido-libro span {
  font-weight: bold;
}
.contenedor-libro .contenido-libro .disponible {
  color: #4CAF50;
}
.contenedor-libro .contenido-libro .no-disponible {
  color: #E51620;
}
.contenedor-libro .contenido-libro .opciones {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}

.alerta {
  color: #E51620;
  text-align: center;
}

.datos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.datos input {
  border: solid 1px #4CAF50;
  height: 2.5rem;
  width: 20rem;
  text-align: center;
  border-radius: 5px;
}
.datos label {
  font-weight: bold;
  color: black;
  text-transform: uppercase;
  margin-top: 2rem;
}

.opciones {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.opciones a {
  margin-bottom: 2rem;
}

.barra {
  background-color: #C0EDFF;
  border: solid 1px #4CAF50;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.barra img {
  width: 25rem;
  margin: 1rem;
}
.barra .cucei {
  width: 5rem;
  height: 6rem;
}

span {
  font-weight: bold;
}

a {
  cursor: pointer;
  color: #4CAF50;
}
a img {
  width: 3rem;
}

.perfil {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  column-gap: 1rem;
  margin-top: 4rem;
  margin-bottom: 10rem;
}

.datosPerfil {
  margin-top: 5rem;
}

.sanciones {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.sanciones a {
  margin-bottom: 5rem;
}

#sanciones table,
#sanciones th,
#sanciones td {
  border: 1px solid black;
  text-align: center;
}
#sanciones tr td {
  border: 1px solid black;
}
#sanciones tr td a {
  color: #3d8b40;
}
#sanciones table {
  margin-top: 5rem;
  margin-bottom: 4rem;
}
#sanciones th {
  color: #113E8E;
}

.verSanciones {
  display: flex;
  margin-bottom: 3rem;
}

.agregar-libro {
  display: none;
}

.ingresar {
  margin-top: 1rem;
}

.margin {
  margin-top: 1rem;
}

.ocultar {
  display: none;
}

.agregar-libro select {
  height: 4rem;
  width: 22rem;
}

.libro-sancionado {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
  border: 2px solid gray;
  margin: 1rem;
}
.libro-sancionado a {
  color: red;
}

.elementos {
  display: flex;
  flex-direction: row;
}
.elementos .elemento-detalle {
  margin-right: 1rem;
}

.elemento-carrito {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 1rem;
}
.elemento-carrito .imagen-carrito {
  width: 10rem;
}

#carrito .botones {
  display: flex;
  flex-direction: row;
}
#carrito .botones button {
  margin-top: 1rem;
}

.detalle-prestamo {
  display: flex;
  flex-direction: column;
}

.contenedor-detalle {
  display: flex;
}

#regresar-catalogo {
  margin: 5rem;
}

#pedido-texto {
  color: #4CAF50;
}

.elemento-detalle {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.elemento-detalle .imagen-detalle img {
  width: 10rem;
  height: 13rem;
}

.prestamos p {
  font-size: 1.5rem;
  font-weight: 200;
}
.prestamos .prestamo {
  border: 2px solid black;
  padding: 10px;
  margin-bottom: 3rem;
}
.prestamos .prestamo .botones a {
  color: white;
  background-color: #113E8E;
  padding: 5px 1.5rem 5px 1.5rem;
  border: 1px solid #4CAF50;
}